@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap);
*,*::before,*::after{box-sizing:border-box;margin:0;padding:0}html{font-family:'Lato', sans-serif}a{text-decoration:none}.App{height:100vh;width:100%;background-image:linear-gradient(to bottom, rgba(0,0,0,0.4), rgba(0,0,0,0.4)),url(/Memory_Card_ReactJS_Project/static/media/pokebg4.38545ef1.png);background-position:center;background-size:100%}.App .scoreboard{width:70%;padding:1rem;background-color:rgba(255,255,255,0.3);color:#f4f4f4;margin:auto;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-around;justify-content:space-around;border-bottom-left-radius:7px;border-bottom-right-radius:7px}.App .scoreboard__banner{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.App .scoreboard__banner img{width:50px;height:50px;object-fit:cover;background-color:#f4f4f4;border-radius:50%;margin-right:0.5rem}.App .scoreboard__banner h1{font-weight:400}.App .scoreboard__scores .top-score{text-align:right;font-size:18px}.App .scoreboard__scores .top-score span{text-decoration:underline}.App .scoreboard__scores .current-score{font-weight:400}.App .game-board{height:85vh;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.App .game-board .cards{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-flex-wrap:wrap;flex-wrap:wrap;grid-gap:5rem;gap:5rem}.App .game-board .cards .card{background-color:rgba(255,255,255,0.5);border-radius:7px;display:-webkit-flex;display:flex;border:1px solid #fff;height:250px;width:200px;-webkit-flex-direction:column;flex-direction:column;cursor:pointer;-webkit-animation:grow 0.5s ease;animation:grow 0.5s ease}.App .game-board .cards .card .image{-webkit-flex:1 1;flex:1 1}.App .game-board .cards .card .image img{width:100%;height:100%;object-fit:cover}.App .game-board .cards .card .poke-name{-webkit-flex:0.2 1;flex:0.2 1;border-top:1px solid #fff;padding:0.3rem 0;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;color:#212121;letter-spacing:0.1rem;font-size:16px}.App .game-board .gameOver-screen{background-color:rgba(255,255,255,0.3);width:300px;padding:2rem 0;border-radius:4px;text-align:center;color:#f4f4f4}.App .game-board .gameOver-screen h4{margin:1rem 0}.App .game-board .gameOver-screen button{padding:7px 17px;border:1px solid #212121;color:#212121;outline:none;border-radius:4px;cursor:pointer;letter-spacing:0.1rem;transition:0.3s all linear}.App .game-board .gameOver-screen button:hover{background-color:rgba(255,255,255,0.8)}.App .game-board .gameOver-screen button:active{-webkit-transform:scale(0.98);transform:scale(0.98)}@-webkit-keyframes grow{from{-webkit-transform:scale(0) rotate(90deg);transform:scale(0) rotate(90deg)}to{-webkit-transform:scale(1) rotate(0deg);transform:scale(1) rotate(0deg)}}@keyframes grow{from{-webkit-transform:scale(0) rotate(90deg);transform:scale(0) rotate(90deg)}to{-webkit-transform:scale(1) rotate(0deg);transform:scale(1) rotate(0deg)}}

